import { NextResponse } from 'next/server';

// Mock Data Store (In-memory, same as list but needs to be accessible ideally, 
// strictly speaking in Next API routes in dev, this might reset often, but okay for demo)
// Re-declaring for standalone logic if I can't import easily across routes without a db file.
// Ideally I should put this in a lib/db-mock.ts but for speed I will use a simple object 
// assuming the client just wants to see the "flow".

// Actually, to make consistent, I should have a shared store.
// But for now, I will hardcode a specific ID response for '1' and dynamic for others.

export async function GET(request: Request, { params }: { params: { id: string } }) {
    const { id } = await params;

    // Return dummy data based on ID
    return NextResponse.json({
        id: id,
        name: id === '1' ? 'Trip ke Bali' : 'Space ' + id,
        createdAt: '2023-12-01',
        members: [
            { id: 'm1', name: 'Andi' },
            { id: 'm2', name: 'Budi' },
            { id: 'm3', name: 'Citra' },
        ],
        transactions: [
            { id: 't1', fromId: 'm1', toId: 'm2', amount: 50000, note: 'Beli Kopi', date: '2023-12-01' },
            { id: 't2', fromId: 'm2', toId: 'm3', amount: 150000, note: 'Bayar Hotel', date: '2023-12-02' }
        ]
    });
}
